@import url(https://fonts.googleapis.com/css?family=Rubik);
* {
  font-family: Rubik;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

p,
li {
  line-height: 1.3;
}

ul {
  padding-left: 2rem;
}

ul > li + li {
  margin-top: 0.5rem;
}

